import React, { useContext } from "react";
import { Box, Typography, styled } from "@mui/material";
import { GetApp as GetAppIcon } from "@mui/icons-material";
import { AccountContext } from "../../../context/AccountProvider";
import { formatDate, downloadFile } from "../../../utils/common-utils";
import { iconPDF } from "../../../data/data";
const Sender = styled(Box)`
	background: #dcf8c6;
	padding: 5px;
	max-width: 60%;
	width: fit-content;
	margin-left: auto;
	display: flex;
	border-radius: 10px;
	word-break: break-word;
`;
const Receiver = styled(Box)`
	background: #fff;
	padding: 5px;
	width: fit-content;
	max-width: 60%;
	display: flex;
	border-radius: 10px;
	word-break: break-word;
`;

const Text = styled(Typography)`
	font-size: 14px;
	padding: 0 25px 0 5px;
`;
const Time = styled(Typography)`
	font-size: 10px;
	color: #919191;
	margin-top: 6px;
	word-break: keep-all;
	margin-top: auto;
`;

const Message = ({ message }) => {
	const { account } = useContext(AccountContext);
	return (
		<>
			{account.sub === message.senderId ? (
				<Sender>
					{message.type === "file" ? (
						<ImageMessage message={message} />
					) : (
						<TextMessage message={message} />
					)}
				</Sender>
			) : (
				<Receiver>
					{message.type === "file" ? (
						<ImageMessage message={message} />
					) : (
						<TextMessage message={message} />
					)}
				</Receiver>
			)}
		</>
	);
};

const TextMessage = ({ message }) => {
	return (
		<>
			<Text>{message?.text}</Text>
			<Time>{formatDate(message?.createdAt)}</Time>
		</>
	);
};

const ImageMessage = ({ message }) => {
	return (
		<div style={{ position: "relative" }}>
			{message?.text?.includes(".pdf") ? (
				<div style={{ display: "flex" }}>
					<img src={iconPDF} alt="pdf-icon" style={{ width: 80 }} />
					<Typography style={{ fontSize: 14 }}>
						{message.text.split("/").pop()}
					</Typography>
				</div>
			) : (
				<img
					src={message.text}
					alt={message.text}
					style={{ width: 300, height: "100%", objectFit: "cover" }}
				/>
			)}
			<Time style={{ position: "absolute", bottom: 0, right: 0 }}>
				<GetAppIcon
					onClick={(e) => downloadFile(e, message.text)}
					fontSize="small"
					style={{
						marginRight: 10,
						border: "1px solid grey",
						borderRadius: "50%",
						cursor: "pointer",
					}}
				/>
				{formatDate(message.createdAt)}
			</Time>
		</div>
	);
};

export default Message;
